body, html {
background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
text-align: center;
}

body h3 {
  font-size: 24px;
  margin-top: 50px;
  font-family: 'Indie Flower', cursive;
}


.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  height: 100vh;
  
}

#quote-box {
  background: rgba(255, 0, 0, 0.151);
  border-radius: 10px;
  box-shadow: 5px 5px 5px 6px #222;
  text-align: center;
  margin-top: 25px;
  margin-left: 15px;
  margin-right: 15px;
  border: 2px inset rgb(40% 50% 60%);;
  width: 500px;
  height: 300px;
  
}

#text-and-author {
  width: 100%;
  height: 50%;
  margin-top: 20px;
}

#text {
  font-size: 20px;
  margin-right: 15px;
  font-family: 'Spirax', cursive;
}

#author {
  font-size: 16px;
  font-family: 'Caveat', cursive;
}

.buttons {
  position: relative;
  width: 100%;
  top: 80px;
}

#new-quote {
  float: right;
  margin-right: 20px;
  border-radius: 3px;
  font-family: 'Vast Shadow', cursive;
}

#quote-icon {
  border: none;
}

.fa-facebook-square {
  float: left;
  margin: 0;
  margin-left: 20px;
  text-decoration: none;
  font-size: 1.75em;
}

.fa-twitter-square {
  background: #55ACEE;
  color: white;
  float: left;
  margin-left: 10px;
  margin-top: 0;
  padding: 5px;
  border-radius: 25%;
  text-decoration: none;
  font-size: 1em;
  
}